import React from "react";
import { Radar } from "react-chartjs-2";
import {
  Chart as ChartJS,
  RadialLinearScale,
  PointElement,
  LineElement,
  Filler,
  Tooltip,
  Legend,
} from "chart.js";

ChartJS.register(
  RadialLinearScale,
  PointElement,
  LineElement,
  Filler,
  Tooltip,
  Legend,
);

const data = {
  labels: ["Javascript", "Html", "Css", "NodeJS", "ReactJS", "VueJS"],
  datasets: [
    {
      label: "My First Dataset",
      data: [7, 6, 5, 6, 7, 6],
      fill: true,
      backgroundColor: "rgba(245, 245, 245, 0.2)",
      borderColor: "rgb(245, 245, 245)",
      pointBackgroundColor: "rgb(245, 245, 245)",
      // pointBorderColor: "rgb(245, 245, 245)",
      // pointHoverBackgroundColor: "rgb(245, 245, 245)",
      // pointHoverBorderColor: "rgb(245, 245, 245)",
    },
    // To fix rendering issue
    {
      label: "",
      data: [0],
      fill: false,
    },
  ],
};

const options = {
  plugins: {
    legend: {
      display: false,
    },
    datalabels: {
      display: false,
    },
    tooltip: {
      enabled: false,
    },
    aspectRatio: 1,
  },
  scales: {
    r: {
      ticks: {
        display: false, // Hides the labels in the middel (numbers)
      },
      angleLines: {
        color: "white",
      },
      grid: {
        color: "white",
      },
      pointLabels: {
        color: "white",
      },
    },
  },
  tooltips: {
    enabled: false,
  },
  aspectRatio: 1,
};
function Skills() {
  return (
    <section className="skills">
      <div className="container">
        <div className="section-title">Skills</div>
        <div style={{ height: "300px", width: "300px" }}>
          <Radar data={data} options={options} type={"radar"} />
        </div>
      </div>
    </section>
  );
}

export default Skills;
