import * as React from "react";
import { SOCIAL_LINKS } from "../utils/Constants";
import Icon from "./Icon/Icon";

function SocialMediaIconLinks(props: { size: number }) {
  const onclickLink = (event: Event, link: string) => {
    event.preventDefault();
    window.open(link);
  };
  return (
    <>
      <Icon
        onClick={(e) => onclickLink(e, SOCIAL_LINKS.LINKED_IN)}
        icon="linkedin"
        size={props.size}
      />
      <Icon
        onClick={(e) => onclickLink(e, SOCIAL_LINKS.GITHUB)}
        icon="github"
        size={props.size}
      />
      <Icon
        onClick={(e) => onclickLink(e, SOCIAL_LINKS.STACK_OVERFLOW)}
        icon="stackoverflow1"
        size={props.size}
      />
      <Icon
        onClick={(e) => onclickLink(e, SOCIAL_LINKS.TWITTER)}
        icon="twitter1"
        size={props.size}
      />
    </>
  );
}

export default SocialMediaIconLinks;
